<template>
  <!-- 主体 -->
  <div class="main-wrapper">
    <!-- 左侧 -->
    <div class="side-wrapper" >
      <div class="slide-wrapper-one">
        <!-- 左侧导航 -->
        <nav-bar style="padding-bottom: 80px">
          <Menu ref="leftMenu" width="100%" theme="dark"  @on-select="selectFirst" :active-name="secondPath">
            <Menu-item v-for="(item,index) in menus" :key="index" :name="item.path">
              <Icon v-bind:type="item.icon" size="30"></Icon>
              <span class="subMenuEllipsis">{{ itemTitle(item.name) }}</span>
            </Menu-item>
          </Menu>
        </nav-bar>
        <!-- 左侧导航 -->
        <!-- 伸缩按钮 -->
        <div class="nav-icon" >
          <Icon type="navicon-round"></Icon>
        </div>
        <!-- 伸缩按钮 /-->
      </div>
      <div class="slide-wrapper-tow">
        <!-- 左侧导航 -->
        <nav-bar style="padding-bottom: 80px">
          <Menu ref="rightMenu" width="100%" theme="dark" :accordion="false" @on-select="selectFn" :active-name="$route.path">
            <Menu-item v-for="(item,index) in menuChild" :key="index" :name="item.path">
              <span class="menuEllipsis">{{ itemTitle(item.name) }}</span>
            </Menu-item>
          </Menu>
        </nav-bar>
        <!-- 左侧导航 -->
      </div>
    </div>
    <!-- 左侧 /-->
    <!-- 内容部分 -->
    <div class="main-container">
      <container>
        <div class="breadData">
          <!-- 面包屑 -->
          <div class="breadcrumb" >
            <Breadcrumb separator=">">
              <span>{{ $t('path') }}：</span>
              <Breadcrumb-item >
                <!--<Icon type="ios-home-outline"></Icon>-->
                {{ $t('home') }}
              </Breadcrumb-item>
              <Breadcrumb-item v-for='(item,index) in breadcrumb' :key="index">
                <!--<Icon :type="pound"></Icon>-->
                {{ itemTitle(item) }}
              </Breadcrumb-item>
            </Breadcrumb>
          </div>
          <!-- 面包屑 /-->
          <!-- 路由 -->
          <transition name="fade" mode="out-in">
            <router-view ></router-view>
          </transition>
        </div>
        <!-- 路由/ -->
      </container>
    </div>
    <!-- 内容部分 /-->
  </div>
  <!-- 主体 /-->
</template>
<script>
  import NavBar from '@/components/layout/navbar/NavBar'
  import Container from '@/components/layout/container/Container'
  import menu from '@/router/menu'
  export default {
    name: 'tabOne',
    components: {
      NavBar,
      Container
    },
    data () {
      return {
        menu: menu // 导航一级菜单
      }
    },
    created () {
      this.$router.push({
        path: this.$store.state.app.tabOneNow
      })
    },
    computed: {
      menus: function () { // 标签过滤数组
        return this.menu.filter(function (number) {
          return number.name !== 'location'
        })
      },
      menuChild: function () {
        this.$nextTick(function () {
          this.$refs.rightMenu.updateActiveName()
        })
        let menuPath = this.menuIndex
        let menuOne = this.menu.filter(function (ber) {
          if (ber.path === menuPath) {
            return ber
          }
        })
        let menuCopy = JSON.parse(JSON.stringify(menuOne[0])) // 深拷贝，到这坑里很多次
        return menuCopy.children.filter(function (number) {
          number.path = '/tabOne' + menuPath + number.path
          return number
        })
      },
      menuIndex () {
        return this.secondPath
      },
      secondPath () {
        return '/' + this.$route.path.split('/')[2]
      },
      thirdPath () {
        return '/' + this.$route.path.split('/')[3]
      },
      state () {
        return this.$store.state.app
      },
      breadcrumb: function () {
        let allName = this.$store.state.app.router.currentPageName
        if (allName.search('/') === -1) {
          return [allName]
        } else {
          return allName.split('/')
        }
      }
    },
    watch: {
      'thirdPath' (val) {
        this.$store.commit('CHANGE_TABONE_NOW', this.$route.path)
      }
    },
    mounted () {
      console.log(this.menuChild)
    },
    methods: {
      /**
       * 选择菜单
       */
      selectFirst (a) {
        this.$router.push({
          path: '/tabOne' + a
        })
      },
      selectFn (a) {
        this.$router.push({
          path: a
        })
      },
      /**
       *菜单menu通过i18n编译再返回
       */
      itemTitle (item) {
        return this.$t(item)
      }
    }
  }
</script>



